import React, { Component } from 'react'

import TodoSection from './TodoSection/TodoSection';
import Navigation from'../Layouts/Navigation'

import { compose } from 'recompose';
import { withFirebase } from '../Firebase';
import { withAuthorization } from '../Session'

import './Home.css';

const HomePage = () => (
    <React.Fragment>
        <Navigation page={'home'}/>
        <Home />
    </React.Fragment>
)

class HomeBase extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            loading: false,
            items: [],
            text: "",
        }
    }

    componentDidMount(){
        this.setState({ loading: true });

        this.props.firebase.todos().on('value', snapshot => {
            const todosObject = snapshot.val();

            const todosList = Object.keys(todosObject).map(key => ({
                ...todosObject[key],
            }));

            this.setState({
                items: todosList,
                loading: false,
            })
        })
    }

    componentWillUnmount(){
        this.props.firebase.todos().off();
        this.props.firebase.todo().off();
    }

    updateChecked = (id) => {
        this.setState({ items: this.state.items.map(item => {
            if(item.id === id){
                item.isCompleted = !item.isCompleted;
            }
            return item;
        })});
    }
/*
    updateDelete = (id) => {
        const deleted = this.state.items.length !== 1 ? (
            this.setState({ 
                items: this.state.items.filter(item =>  item.id !== id)
            })) : alert("Cannot delete last item");
    }*/

    handleSubmit = (event) => {
        event.preventDefault();

        const normalizedCurrentDate = (
            JSON.stringify(new Date())
            .substring(1, 25));

        
        const todoKey = this.props.firebase.todos().push().key;    
        
        const newTodo = {
            id: todoKey,
            title: this.state.text,
            isCompleted: false,
            numberLikes: 0,
            currentDateTime: normalizedCurrentDate
        }
        this.props.firebase.todo(todoKey).set(newTodo);
        this.cancel();
    }

    cancel = () => {
        this.setState({text: ""});
    }

    updateText = (event) => {
        this.setState({text: event.target.value})
    }

    handleStarClick = (id) => {
        var incrementedTodo = undefined;
        this.props.firebase.todo(id).on('value', function(snapshot){
            var todo = snapshot.val();
            incrementedTodo = {
                id: id,
                title: todo.title,
                numberLikes: todo.numberLikes + 1,
                currentDateTime: todo.currentDateTime,
            }
        })

        this.props.firebase.todo(id).set(incrementedTodo);
    }

    render() {
        return (
            <React.Fragment>
                <div className="logo-container">
                    <img className="logo-jumbotron" src={require('../../assets/you-do-logo.png')} alt="YOU-DO" />
                </div>
                <div className="container-home">
                    <div className="background-image"/>
                        <div className="submit-box-wrapper">
                            <form onSubmit={this.handleSubmit}>
                                <input 
                                    className='form-control mr-sm-2'
                                    type="text"
                                    id="submit-box"
                                    placeholder="Make your mark"
                                    maxLength="280"
                                    value={this.state.text}
                                    onChange={this.updateText}
                                />
                            </form>
                        </div>
                    </div>
                
                <TodoSection todo={this.state.items} incrementStar={this.handleStarClick}/>
            </React.Fragment>
        )
    }
}

const condition = authUser => authUser

const Home = withFirebase(HomeBase)

export default HomePage;

export { Home };
